export default {
	data() {
		return {
			list: [],
			list2:[],
			tabCur: 0,
			mainCur: 0,
			verticalNavTop: 0
		};
	},
	onLoad(){
		(async ()=>{
			await this.selectCategory();
			if(this.list.length>0){
				this.selectProductsByCategory(this.list[0].id);
			}
		})();
	},
	methods: {
		async selectProductsByCategory(id) {
			const res = await this.$util.request({
				requestUrl: 'api/productsByCategory/'+id,
			});
			console.log('某类下的商品：', res);
		},
		async selectCategory() {
			const res = await this.$util.request({
				requestUrl:'api/category'
			});
			console.log('查询所有类别：',res);

			if(res!==undefined){
				this.list = res.data.data;
			}
		},
		TabSelect(e) {
			this.tabCur = e.currentTarget.dataset.id;
			this.mainCur = e.currentTarget.dataset.id;
			this.verticalNavTop = (e.currentTarget.dataset.id - 1) * 50;
			
			this.selectProductsByCategory(this.list[this.tabCur].id);
		},
		VerticalMain(e) {
			let that = this;
			let tabHeight = 0;

			for (let i = 0; i < this.list.length; i++) {
				let view = uni.createSelectorQuery().select('#main-' + i);
				view.fields(
					{
						size: true
					},
					data => {
						this.list[i].top = tabHeight;
						tabHeight = tabHeight + data.height;
						this.list[i].bottom = tabHeight;
					}
				).exec();
			}

			let scrollTop = e.detail.scrollTop + 10;
			for (let i = 0; i < this.list.length; i++) {
				if (scrollTop > this.list[i].top && scrollTop < this.list[i].bottom) {
					this.verticalNavTop = (this.list[i].id - 1) * 50;
					this.tabCur = this.list[i].id;
					return false;
				}
			}
		}
	}
};
